@charset "UTF-8";
/*
 Common Styles
------------------------------ */
body {
  margin: 0;
  padding: 0;
  background-color: #ccc;
  color: #333;
  font-size: 15px;
  line-height: 2;
  display: none; }

p, h1, h2, h3, h4, h5, h6 {
  margin-top: 0; }

img {
  vertical-align: bottom; }

ul {
  margin: 0;
  padding: 0; }

li {
  list-style: none; }

a {
  color: #3583aa;
  text-decoration: none; }

a:visited {
  color: #788d98; }

a:hover {
  text-decoration: none; }

header {
  width: 960px;
  height: 100px;
  margin: 0 auto; }

.logo {
  float: left;
  margin-top: 48px;
  font-size: 35px;
  line-height: 1;
  text-shadow: 1px 1px 1px #000;
  font-family: 'Bitter', serif; }

.logo a {
  color: #fff;
  display: block; }

.logo a:hover {
  text-decoration: none; }

.logo a:first-letter {
  font-size: 1.5em; }

.global-nav {
  float: right;
  margin-top: 60px; }

.global-nav li {
  float: left;
  margin: 0 20px;
  font-size: 20px;
  list-style: none;
  text-shadow: 1px 1px 1px #000;
  font-family: 'Bitter', serif; }

.global-nav li a {
  color: #fff; }

.global-nav li.current a,
.global-nav li a:hover {
  border-bottom: 3px solid #fff;
  padding-bottom: 3px;
  text-decoration: none; }

#wrap {
  clear: both;
  background-color: #fff;
  margin-top: 50px;
  padding: 35px 0; }

.content {
  width: 960px;
  margin: 0 auto; }

footer {
  text-align: center;
  padding: 20px 0;
  background-color: #767671; }

footer a {
  color: #fff; }

footer a:visited {
  color: #fff; }

footer small {
  font-size: 12px; }

footer small.copyright .copy-years {
  margin-right: 0.5em; }

.main-center {
  width: 940px;
  min-height: 350px;
  margin: 0 auto; }

section {
  margin-bottom: 35px; }

table {
  border-spacing: 0;
  border-collapse: collapse; }

.clearfix:after {
  content: "";
  display: block;
  clear: both; }

h1 {
  font-family: 'Bitter', serif;
  font-size: 36px;
  border-bottom: 1px solid #ccc; }

h2 {
  font-family: 'Bitter', serif;
  font-size: 24px; }

.icon:before {
  content: "";
  padding-right: 10px;
  border-left: 7px solid #ccc; }

.pagetop {
  display: none;
  position: fixed;
  bottom: 65px;
  right: 15px;
  width: 50px;
  height: 50px;
  overflow: hidden;
  cursor: pointer; }

.pagetop a {
  display: block;
  width: 50px;
  height: 50px;
  background-color: #ccc;
  text-align: center;
  color: #fff;
  text-decoration: none; }

.pagetop a:before {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  border: 3px solid #ccc;
  border-left-color: #fff;
  border-top-color: #fff;
  top: 20px;
  left: 12px;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg); }

.pagetop a:hover {
  opacity: 0.8; }

.btn a {
  background-color: #00bbdd;
  color: #fff;
  font-size: 20px;
  font-family: 'Bitter', serif;
  width: 185px;
  display: block;
  text-align: center;
  line-height: 50px;
  margin-top: 20px;
  border-radius: 5px;
  border: 3px solid #00bbdd;
  -webkit-transition: background-color 0.2s linear;
  -o-transition: background-color 0.2s linear;
  transition: background-color 0.2s linear; }

.btn a:hover {
  text-decoration: none;
  background-color: #fff;
  color: #00bbdd; }

/*
 Index
------------------------------ */
#index {
  background-image: url(../images/bg_index.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  background-size: cover;
  position: relative; }

#index #wrap {
  margin-top: 0;
  padding: 0;
  background-color: transparent; }

#index footer {
  width: 960px;
  margin: 150px auto 0 auto;
  text-align: left;
  background-color: transparent; }

#index footer .copyright {
  font-size: 16px;
  text-shadow: 1px 1px 1px #000; }

#index .content {
  margin-top: 50px;
  width: 100%;
  padding: 100px 25px 25px;
  background: rgba(0, 0, 0, 0.2);
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

#index .content .content-inner {
  width: 960px;
  margin: 0 auto; }

#index .content h1 {
  font-size: 100px;
  line-height: 1;
  color: #fff;
  font-family: 'Bitter', serif;
  margin-bottom: 50px;
  border: none;
  text-shadow: 1px 1px 1px #000; }

#index .content p.catch {
  font-size: 16px;
  color: #fff;
  text-shadow: 1px 1px 3px #000; }

/*
 About
------------------------------ */
#about {
  background-image: url(../images/bg_about.jpg);
  background-repeat: no-repeat;
  background-position: center top;
  background-attachment: fixed;
  background-size: 100% auto; }

#about .profile-txt {
  width: 540px;
  float: left; }

#about .profile-txt .profile-name {
  font-weight: bold; }

#about .profile-txt .profile-link {
  margin-left: 1em;
  margin-bottom: 1em; }

#about .profile-txt .profile-link li {
  list-style-type: disc; }

#about .profile-txt .profile-link a {
  text-decoration: underline; }

#about .profile-txt .profile-link a:hover {
  text-decoration: none; }

#about .profile-image {
  float: right;
  width: 340px; }

#about .profile-image img {
  max-width: 100%;
  height: auto; }

#about .career th {
  width: 240px;
  background-color: #f0f0f0;
  padding: 12px 0;
  border: 1px solid #ccc; }

#about .career td {
  width: 660px;
  padding: 12px 40px;
  border: 1px solid #ccc; }

.profile-skill-tool {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  margin-bottom: 30px; }

.profile-skills,
.profile-tools {
  -ms-flex-preferred-size: 50%;
  flex-basis: 50%; }

/*
 Portfolio
------------------------------ */
#portfolio {
  background-image: url(../images/bg_portfolio.jpg);
  background-repeat: no-repeat;
  background-position: center top;
  background-attachment: fixed;
  background-size: 100% auto; }

#portfolio .main-center {
  min-height: 500px; }

#portfolio .photograph-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  padding: 10px; }

#portfolio .photograph-list li {
  width: calc(33.3% - 10px);
  margin: 5px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

#portfolio .photograph-list li a {
  display: block;
  width: 100%;
  height: 100%;
  color: #333;
  text-decoration: none; }

#portfolio .photograph-list li a:hover {
  opacity: 0.7; }

#portfolio .photograph-list li h3 {
  font-size: 1.2rem;
  margin-bottom: 0; }

#portfolio .photograph-list li p {
  padding: 10px; }

#portfolio .photograph-list li img {
  max-width: 100%; }

/*
 画面サイズが960px以下の場合に適用
------------------------------ */
@media screen and (max-width: 960px) {
  /* common styles */
  body {
    font-size: 3.5vw; }
  h1 {
    font-size: 7.2vw; }
  h2 {
    font-size: 6vw; }
  header {
    padding: 0 15px 25px;
    width: 100%;
    height: 250px;
    margin: 0 auto;
    -webkit-box-sizing: border-box;
    box-sizing: border-box; }
  .logo {
    float: left;
    margin-top: 48px;
    font-size: 7.2vw; }
  .global-nav {
    float: right;
    margin-top: 60px; }
  .global-nav li {
    float: left;
    margin: 0 20px;
    font-size: 3.5vw; }
  #wrap {
    margin-top: 40px;
    padding: 35px 15px 15px; }
  .content {
    width: 100%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box; }
  .main-center {
    width: 100%;
    min-height: auto;
    -webkit-box-sizing: border-box;
    box-sizing: border-box; }
  #index .content .content-inner {
    width: auto; }
  .main {
    float: none;
    width: auto;
    margin: 0 5%; }
  .pagetop {
    right: 0;
    bottom: 65px; }
  /* #index */
  #index .content {
    padding: 25px;
    margin-top: 60px; }
  #index .content h1 {
    font-size: 7.2vw;
    text-align: center;
    margin-bottom: 25px; }
  #index .content p.catch {
    font-size: 3.5vw; }
  #index .content .btn {
    text-align: center;
    margin-bottom: 0; }
  #index .content .btn a {
    display: inline-block;
    font-size: 3.5vw;
    line-height: 44px; }
  #index footer {
    width: 100%;
    margin: 0 auto;
    text-align: center; }
  #index footer .copyright {
    font-size: 3.5vw; }
  /* #about */
  #about .profile-txt {
    float: none;
    width: 100%; }
  #about .profile-image {
    float: none;
    width: 100%; }
  #about .profile-image img {
    width: 100%;
    height: auto; }
  #about .career th,
  #about .career td {
    display: block;
    width: 100%;
    border: none;
    padding: 10px 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box; }
  #about .career th {
    padding: 10px;
    text-align: left; }
  /* #portfolio */
  #portfolio .photograph-list li {
    width: calc(50% - 10px); }
  #portfolio .photograph-list li img {
    width: 100%; } }

/*
 画面サイズが600px以下の場合に適用
------------------------------ */
@media screen and (max-width: 600px) {
  /* common styles */
  header {
    width: auto;
    height: auto;
    margin: 0 5%; }
  footer {
    width: 100%;
    font-size: 3.5vw; }
  .logo {
    float: none;
    margin-top: 60px;
    text-align: center;
    font-size: 7.2vw; }
  .global-nav {
    float: none;
    margin-top: 40px;
    text-align: center; }
  .global-nav li {
    display: inline;
    float: none;
    margin: 0 8px; }
  #wrap {
    margin-top: 0; }
  /* #index */
  #index .content {
    margin-top: 0; }
  /* #about */
  #about {
    background-image: url(../images/bg_about.jpg); }
  .profile-skill-tool {
    display: block; }
  .profile-skills,
  .profile-tools {
    width: 100%; }
  /* #portfolio */
  #portfolio {
    background-image: url(../images/bg_portfolio.jpg); }
  #portfolio .photograph-list li {
    width: 100%;
    margin: 0; } }
